.Slideshow {
  position: relative;
  width: 100vw;
  background: #333;
  height: 100vh;
  scroll-snap-align: start;
}

.Slideshow > img {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  opacity: 0;
  transition: opacity 0.2s;
}

.active {
  opacity: 1;
}
