@import url("https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@300;700&display=swap");

body {
  margin: 0;
  font-family: "Source Code Pro", monospace;
  background: #000;
}

.App {
  overflow: scroll;
  height: 100vh;
  scroll-snap-type: y mandatory;
  scroll-behavior: smooth;
}

.downloadSection {
  position: relative;
  min-height: 100vh;
  background: #1b1b1b;
  overflow: hidden;
  scroll-snap-align: start;
  display: flex;
  align-items: stretch;
  /* justify-content: space-evenly; */
  padding-right: 10vw;
  padding-left: 20vw;
  /* padding-bottom: 20vh; */
  flex-direction: column;
}

.downloadSection > svg {
  position: absolute;
  right: -40vw;
  top: -40vh;
  width: 100vw;
  fill: #e44444;
  animation: blobMove 15s ease-in-out infinite;
  /* fill: url(#pattern); */
}

.downloadSection > h3 {
  font-size: 12vh;
  color: white;
  /* mix-blend-mode: difference; */
  text-align: right;
  z-index: 9;
}

.downloadSection > a {
  font-size: 4vh;
  color: white;
  /* mix-blend-mode: difference; */
  text-align: right;
  padding: 0px;
  margin: 10px;
  z-index: 9;
}

.aboutSection {
  min-height: 100vh;
  scroll-snap-align: start;
  background: #000;
  color: #fff;
  padding: 100px;
  padding-bottom: 0px;
}

.joinUsSection {
  min-height: 100vh;
  scroll-snap-align: start;
  background: #000;
  color: #fff;
  padding: 100px;
  padding-bottom: 0px;
}

@keyframes blobMove {
  0% {
    transform: scale(1) rotate(0deg);
  }
  20% {
    transform: scale(0.9, 1) rotate(60deg);
  }
  40% {
    transform: scale(0.9) rotate(40deg);
  }
  60% {
    transform: scale(1.2) rotate(70deg);
  }
  80% {
    transform: scale(1.3) rotate(30deg);
  }
  100% {
    transform: scale(1) rotate(0deg);
  }
}
