.Topbar {
  position: fixed;
  left: 0px;
  width: 250px;
  top: 0px;
  bottom: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;
  /* background: red; */
  z-index: 5;
  padding: 20px;
  pointer-events: none;
}

.Topbar > a {
  display: block;
  position: relative;
  width: 200px;
  height: 50px;
  margin: 0px;
  text-align: left;
  color: white;
  mix-blend-mode: difference;
  pointer-events: all;
}

.Topbar > a > svg {
  display: inline-block;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 25px;
  fill: #fff;
}

.Topbar > a > p {
  mix-blend-mode: difference;
  display: inline-block;
  position: absolute;
  top: -18px;
  left: 40px;
  opacity: 0;
  transition: opacity 0.2s;
  padding: 0px;
  font-size: 18px;
}

.Topbar:hover > a > p {
  opacity: 1;
}

.Topbar:hover > a:hover > p {
  font-weight: 900;
}
